ul {
    list-style-type: none;
    margin: 0 ;
    padding: 0;
    overflow: hidden;
    color: #fff;
    background-color:#222831;
  }
  
  li {
    float: left;
    margin:  0 0 0 10px
  }
  li img {
    width: 50px;
    height: 46px;
  }
  
  li a {
    display: block;
    color: #eeeeee;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  li a:hover:not(.active) {
    background-color: #00adb5;
    color: #fff
  }
  
 
  .iconsize{
   float: right;
   color: #fff;
   margin-right: 15px;
   margin-top: 15px;
   cursor: pointer;
  
  }

  .iconsize:hover{
    color: #FFF
  }
.list-type1{
    width:400px;
    margin:0 auto;
    }
    
    .list-type1 ol{
    counter-reset: li;
    list-style: none;
    *list-style: decimal;
    font-size: 15px;
    font-family: 'Raleway', sans-serif;
    padding: 0;
    margin-bottom: 4em;
    display: grid
    }

    .list-type1 ol ol{
    margin: 0 0 0 2em;
    }
    
    .list-type1 a{
    position: relative;
    display: inline-block;
    padding: .4em .4em .4em 2em;
    *padding: .4em;
    margin: .5em 0;
    background: #93C775;
    color: #000;
    text-decoration: none;
    border-radius: 10em;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    width: 100%
    }
    
    .list-type1 a:hover{
    background: #d6d4d4;
    color: #fff;
    text-decoration:none;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    }
    
    .list-type1 a:before{
    content: counter(li);
    counter-increment: li;
    position: absolute;
    left: -1.3em;
    top: 50%;
    margin-top: -1.3em;
    background:#93C775;
    height: 2em;
    width: 2em;
    line-height: 2em;
    border: .3em solid #EEEEEE;
    text-align: center;
    font-weight: bold;
    border-radius: 2em;
    color:#FFF;
    }
p{
    font-size: 12px;
    float: left;
}
* {
    box-sizing: border-box;
  }
  
  .task-text{
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
  }
  
  label {
    padding: 12px 12px 12px 0;
    display: inline-block;
  }
  
  .add-task{
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
  }
  
  input[type=submit]:hover {
    background-color: #45a049;
  }
  
  .container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
  }
  
  .col-25 {
    float: left;
    width: 25%;
    margin-top: 6px;
  }
  
  .col-75 {
    float: left;
    width: 75%;
    margin-top: 6px;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both;
  }

  .tabelBu {
    margin-right: 10%;
    width: 80%
  }
  
  @media screen and (max-width: 600px) {
.col-25, .col-75, input[type=submit] {
      width: 100%;
      margin-top: 0;
    }
}
